<template>
<section>
  <el-row>
      <el-col :span="24">
        <span class="top-nav-link" @click="goBack">返回上一页</span>
      </el-col>
  </el-row>
  <el-row class="bianzheng-box">
    <el-col :span="4" align='center' class="top-bar">
          <span class="search-title">主症选择</span>
      </el-col>
      <el-col :span="18" class="top-bar">
          <el-autocomplete suffix-icon="el-icon-search"
          style="width: 100%;"
          v-model="empBasic.mainSymptomName"
          :fetch-suggestions="querySearch"
          placeholder="请输入主症名称或拼音码"
          @select="handleSelect"
          ></el-autocomplete>
      </el-col>
  </el-row>
  <div class="bianzheng-main">
    <div class="main-item">
      <div class="item-title">兼症选择</div>
        <el-table
          @select="handleSelectJZ"
         :data="jzData" max-height="600" style="width: 100%" :show-header='false'>
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column align="center" prop="diseaseName" label="兼症名称"></el-table-column>
      </el-table>
    </div>
    <div class="main-item">
      <div class="item-title">舌相选择</div>
        <el-table :data="sxData" max-height="400" style="width: 100%" :show-header='false'
        ref="singleTableSX"
        @cell-click='handleRowClick'
        @select="handleSelectSX">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column align="center" prop="tongueName" label="兼症名称"></el-table-column>
      </el-table>
      <img class="img-box" :src="imgSrc">
    </div>
    <div class="main-item">
      <div class="item-title">脉象选择</div>
      <el-table :data="mxData" max-height="600" style="width: 100%" :show-header='false'
      ref="singleTableMX"
      @select="handleSelectMX">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column align="center" prop="pulseName" label="兼症名称"></el-table-column>
      </el-table>
    </div>
  </div>
  <el-row class="toolbar">
    <el-col :span="23" align="right">
      <el-button type="primary" @click="handelNextPage">下一步</el-button>
    </el-col>
  </el-row>
</section>
</template>
<script>
import {
  getSelectMainSymptomTable,
  getMainSymptomList, // 针灸使用
  getSelectMainSymptomByCode, // 肛肠使用
  getDiseaseTable,
  getTongueTable,
  getPulseTable,
  updateEmpBasic, // 保存病例信息
} from '../../api'
import { mapState} from 'vuex'
export default {
  computed:{
    ...mapState('patient',{
      mecidalNum: state => state.mecidalNum,
      empBasicMainID:state=>state.empBasicMainID,
      deptCode:state=>state.deptCode
    }),
  },
  data(){
    return{
      empBasic: {  // 病例信息
        empBasicMainID: '',
        mainSymptomName: '' , // 主症名称
        mainSymptomCode: '', // 主症编码
        diseaseName: '', // 兼症名称
        diseaseCode: '', // 兼症编码
        tongueName: '', // 舌像名称
        tongueCode: '',// 舌像编码
        pulseName: '', // 脉象名称
        pulseCode: '', // 脉象编码
      },
      restaurants: [], // 主症建议列表
      jzData: [], // 兼症列表
      sxData: [], // 舌相
      mxData: [], // 脉相
      imgSrc: '../../static/img/nopic.png', // 图片
    }
  },
  methods:{
    handelSaveEmpBasic(){ // 保存 主症数据
      this.empBasic.empBasicMainID = this.empBasicMainID
      // if (this.empBasic.mainSymptomName =='' ||
      //     this.empBasic.diseaseName =='' ||
      //     this.empBasic.pulseName =='' ||
      //     this.empBasic.tongueName ==''
      //   ){
      //   this.$message.error('缺少选择项 请选择完整')
      //   return
      // }
      updateEmpBasic(this.empBasic).then(res=>{
        if(res.data.code == '0'){
          // 本地存储下页discriminate 展示使用
          sessionStorage.setItem('zy_diseaseName',this.empBasic.diseaseName)
          sessionStorage.setItem('zy_mainSymptomName',this.empBasic.mainSymptomName)
          sessionStorage.setItem('zy_pulseName',this.empBasic.pulseName)
          sessionStorage.setItem('zy_tongueName',this.empBasic.tongueName)
          sessionStorage.setItem('zy_imgSrc',this.imgSrc)
          this.$router.push('discriminate')
        }else{
          this.$message.error('保存错误')
        }
      })
    },
    querySearch(queryString, cb){ // 主症结果搜索
      var restaurants = this.restaurants
      let results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      cb(results)
    },
     createFilter(queryString) {
        return (restaurant) => {
          // return (restaurant.pinYinCode.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
          return (restaurant.pinYinCode.toLowerCase().indexOf(queryString.toLowerCase()) === 0
          || restaurant.mainSymptomName.toLowerCase().includes(queryString.toLowerCase())
          );
        }
    },
    handleSelect(item){ // 选择主症
      this.empBasic.mainSymptomCode = item.mainSymptomCode
      // 获取兼症
      getDiseaseTable({mainSymptomCode: item.mainSymptomCode}).then(res=>{
        if(res.data.code=='0'){
          this.jzData = res.data.data
        }
      })
      // 获取舌相
      getTongueTable({mainSymptomCode: item.mainSymptomCode}).then(res=>{
        if(res.data.code=='0'){
          this.sxData = res.data.data
        }
      })
      // 获取脉相
      getPulseTable({mainSymptomCode: item.mainSymptomCode}).then(res=>{
        if(res.data.code=='0'){
          this.mxData = res.data.data
        }
      })
    },
    handleSelectionChange(value){ // 数据选择
      console.log(value)
    },
    handleSelectJZ(item){ // 兼症 多选
      this.empBasic.diseaseName = ''
      this.empBasic.diseaseCode = ''
      item.forEach(element => {
        this.empBasic.diseaseName += element.diseaseName + ";"
        this.empBasic.diseaseCode += element.diseaseCode + ";"
      });
    },
    handleSelectSX(item, row){ // 舌相 单选 tongueName tongueCode
      this.$refs.singleTableSX.clearSelection()
      this.$refs.singleTableSX.toggleRowSelection(row)
      this.empBasic.tongueName = row.tongueName
      this.empBasic.tongueCode = row.tongueCode
      if(row.tongueImage){
        // this.imgSrc = 'data:image/png;base64,' + row.tongueImage
        this.imgSrc = row.tongueImage
      }else{
        this.imgSrc = '../../static/img/nopic.png'
      }
    },
    handleSelectMX(item, row){ // 脉相 单选 pulseName pulseCode
      this.$refs.singleTableMX.clearSelection()
      this.$refs.singleTableMX.toggleRowSelection(row)
      this.empBasic.pulseName = row.pulseName
      this.empBasic.pulseCode = row.pulseCode
    },
    handleRowClick(row){
      this.$refs.singleTableMX.clearSelection()
      this.$refs.singleTableMX.toggleRowSelection(row)
    },
    handelNextPage(){ // 下一页
      this.handelSaveEmpBasic()
    }
  },
  mounted(){
    // let westernMedicineCode = sessionStorage.getItem('zhzy_gcxt_xybm_code')
    // if(westernMedicineCode){
    //   getSelectMainSymptomByCode({
    //     deptCode: sessionStorage.getItem('current_deptCode'),
    //     userCode: sessionStorage.getItem('usercode'),
    //     westernMedicineCode: westernMedicineCode
    //   }).then(res=>{
    //     if(res.data.code=='0'){
    //     let arr = res.data.data
    //     for( let element of arr){
    //       element.value = element.mainSymptomName
    //     }
    //     this.restaurants = arr
    //     return
    //     }
    //   this.$message.error('获取主症失败!')
    //   })
    //   return
    // }
    // 根据科室获取不同数据
    let funGetList = null
    let requestData = {
      deptCode: sessionStorage.getItem('current_deptCode'),
      userCode: sessionStorage.getItem('usercode'),
    }
    let currentDeptCode = sessionStorage.getItem('current_deptCode')
    if(currentDeptCode == '10004'){ // 肛肠
      requestData.westernMedicineCode = sessionStorage.getItem('zhzy_gcxt_xybm_code')
      funGetList = getSelectMainSymptomByCode
    }
    // if(currentDeptCode == '10008'){ // 针灸
    //   funGetList = getSelectMainSymptomByCode
    // }
    if(currentDeptCode == '10001'){ //临证治疗
      funGetList = getSelectMainSymptomTable
    }
    if(!funGetList){
      this.$message.warning('没有正确的科室数据')
      return
    }
    funGetList(requestData).then(res=>{
      if(res.data.code=='0'){
        let arr = res.data.data
        for( let element of arr){
          element.value = element.mainSymptomName
        }
        this.restaurants = arr
        return
      }
      this.$message.error('获取主症失败!')
    })
  }
}
</script>
<style scoped>
.bianzheng-box{
  margin: 10px 22px;
  background: #FAF7F6;
}
.bianzheng-box .top-bar{
  padding: 16px 0;
}
.bianzheng-box .search-title{
  line-height: 38px;
  font-size: 18px;
  color: #666;
}
.bianzheng-main{
  display: flex;
  margin: 10px 22px;
  padding-top: 12px;
  justify-content: space-between;
}
.main-item{
  position: relative;
  height: 640px;
  width: 30%;
  background-color: #FAF7F6;
}
.main-item .img-box{
  position: absolute;
  width: 160px;
  left: 50%;
  bottom: 0px;
  /* border: 2px solid #fff; */
  transform: translate(-50%, -20px);
}
.main-item .item-title{
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
  color: #666666;
  border-bottom: 1px solid #ccc;
}
/* 表格无数据 */
.el-table__empty-block{
  background-color: #FAF7F6 !important;
}

</style>



